.slick-next:before {
  background-image: url("../../../../icons/arrow-orange.svg");
  transform: rotate(270deg);
  content: "";
  width: 40px;
  height: 40px;
  background-size: contain;
  background-repeat: no-repeat;
  display: block;
  margin-left: -15px;
}

.slick-prev:before {
  transform: rotate(90deg);
  background-image: url("../../../../icons/arrow-orange.svg");
  content: "";
  width: 40px;
  height: 40px;
  background-size: contain;
  background-repeat: no-repeat;
  display: block;
}

.slick-list {
  display: flex;
}

.slick__slide {
  background-color: var(--color-white);
  border-radius: 10px 10px 0 0;
  margin-right: 15px;
  height: 100%;
  display: flex;
  flex-direction: column;
}
@media (max-width: 991px) {
  .slick__slide {
    margin-right: 10px;
    margin-left: 10px;
  }
}

.slick__arrow {
  width: calc(100% + 85px);
  left: -40px;
}
@media (max-width: 991px) {
  .slick__arrow {
    width: calc(100% + 64px);
    left: -23px;
  }
}

.slick-track {
  display: flex;
}

.highlights-wrapper .slick-prev:before, .highlights-wrapper .slick-next:before {
  background-image: url("../../../../icons/arrow-white.svg");
}

.slide__caption {
  padding: 0;
}

.slide__content {
  height: 100%;
  display: flex;
}

.slide__description {
  display: flex;
  height: 100%;
  flex-direction: column;
}
.slide__description > * {
  height: 100%;
}

.slick-arrow {
  background: transparent;
}